@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100..900&family=Kelly+Slab&display=swap");

body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;

  margin: 0;
  padding: 0;
  background-color: white;
}

html {
  overflow-x: hidden;
}

.container {
  max-width: 1200px !important;
  overflow: hidden;
  margin-inline: auto;
  padding: 10px;
}

.streak-container {
  margin: 0 auto;
  width: 100px;
  height: 100px;
  border: 6px solid #f16b10;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.kelly-slab-regular {
  font-family: "Kelly Slab", sans-serif;
  font-weight: 700;
  font-style: normal;
}

.form {
  margin: 0 auto;
  background-color: #f5f5f5;
  padding: 20px 30px;

  margin-top: 70px;

  border-radius: 30px;
  max-width: 500px;
  display: flex;
  justify-content: space-between;
}

.blue-gradient {
  left: 45%;
  top: 25%;
  width: 500px; /* Increase width to spread the color */
  height: 500px; /* Increase height */
  position: absolute; /* Use absolute positioning */

  gap: 0px;
  opacity: 0.9; /* Lower opacity for a subtle effect */
  /* Larger blur to soften the edges */
  border-radius: 50%; /* Keep it circular */

  filter: blur(180px);
  /* Rotate if desired */
  transform: rotate(-23.02deg);

  /* Soft gradient color spread */
  background-image: linear-gradient(
    rgba(108, 96, 254, 1),
    rgba(108, 96, 254, 1)
  );
  /* Prevent interaction with the element */
  pointer-events: none;
}

.red-gradient {
  left: 20%;
  top: 20%;
  width: 500px; /* Increase width to spread the color */
  height: 500px; /* Increase height */
  position: absolute; /* Use absolute positioning */

  gap: 0px;
  opacity: 0.9; /* Lower opacity for a subtle effect */
  /* Larger blur to soften the edges */
  border-radius: 50%; /* Keep it circular */

  filter: blur(180px);
  /* Rotate if desired */
  transform: rotate(-23.02deg);

  /* Soft gradient color spread */
  background-image: linear-gradient(rgb(254, 96, 172), rgb(254, 96, 246));
  /* Prevent interaction with the element */
  pointer-events: none;
}

.curve {
  display: none;
  position: absolute;
  /* left: 50%; */
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to top right,
    red calc(50% - 1px),
    red,
    green calc(50% + 1px)
  );
  transform: translateX(-100px);
  transform: rotateX(70deg);
}
